<template>
  <div>
    <header-bar></header-bar>
    <p class="title"></p>
    <div>
      <mt-field label=""   placeholder=""></mt-field>
      <mt-field label=""  type="number"  placeholder=""></mt-field>
      <mt-field label=""  type="email" placeholder=""></mt-field>
    </div>
    <p class="title"></p>
    <div>
      <mt-field label=""   placeholder=""></mt-field>
      <mt-field label=""  type="number"  placeholder=""></mt-field>
      <mt-field label="" placeholder=""></mt-field>
    </div>
    <footer class="btn">
      <mt-button  size="large" style="font-size: 15px;color: white;}" :style="{background: all.window.navigationBarBackgroundColor}"></mt-button>
    </footer>
  </div>
</template>
<script>
  import headerBar from '../header.vue'
export default{
    data(){
        return {

        }
    },
  computed:{
    all(){
      return this.$store.getters.getAll;
    }
  },
  components:{
    headerBar
  }
}
</script>
<style scoped>
  .title{
    padding: 0.2rem;
    background: #f7f7f7;
  }
  .btn{
    width: 90vw;
    padding: 2px 0;
    margin: 2vh 5vw;
  }
</style>



